@import "./styles/config";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  background: #f1f1f1;
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  padding: 30px;
  border-radius: $radius;
  position: relative;

  section {
    min-height: 290px;
  }
}

@import "./styles/header";
@import "./styles/sorter";
@import "./styles/btn";
@import "./styles/task";
@import "./styles/tasks";
@import "./styles/addTaskForm";
@import "./styles/msg";
@import "./styles/footer";
